.contact {
  margin-bottom: s(200);
  &__container {
    display: flex;
    border-radius: s(5);
    padding: s(70) s(100) s(85);
    background-color: #303032;
  }
  &__box {
    &--left,
    &--right {
      flex-basis: 50%;
    }
    &--left {
      position: relative;
      padding-top: s(34);
      border-right: s(1) solid rgba(255, 255, 255, 0.2);
      & svg {
        position: absolute;
        bottom: s(-84);
        left: s(-98);
        @include block(s(1608), s(182));
      }
    }
  }
  &__title {
    margin-bottom: s(20);
  }
  &__desc {
    width: 100%;
    max-width: s(410);
    font-family: 'GraphikLCG';
    font-size: s(20);
    font-weight: 300;
    line-height: 130%;
    letter-spacing: 0.015em;
  }
  &__form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding: s(38) 0 0 s(60);
    &-desc {
      width: 100%;
      max-width: s(570);
      margin-bottom: s(40);
      font-family: 'GraphikLCG';
      font-size: s(20);
      font-weight: 300;
      line-height: 130%;
      letter-spacing: 0.015em;
      color: rgba(255, 255, 255, 0.3);
    }
  }
  &__label {
    position: relative;
    display: block;
    width: 100%;
    margin-bottom: s(39.5);
    & .placeholder {
      position: absolute;
      overflow: hidden;
      opacity: 0;
      font-size: 0;
      @include block(0);
    }
    &:last-of-type {
      margin-bottom: s(42);
    }
  }
  &__input {
    width: 100%;
    padding: s(10) 0;
    border-width: 0 0 s(1) 0;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.3);
    @include transitions((border-color, border-width));
    &,
    &::placeholder {
      text-transform: uppercase;
      font-size: s(16);
      font-weight: 700;
      line-height: 160%;
      letter-spacing: 0.06em;
    }
    & + span {
      position: absolute;
      left: 0;
      bottom: 0;
      content: '';
      @include block(100%, s(3));
      background-color: #d33a45;
      @include transition(opacity, 0);
    }
    &:hover,
    &:focus,
    &:active {
      & + span {
        opacity: 1;
      }
    }
  }
  &__btn {
    position: relative;
    overflow: hidden;
    padding: s(14) s(27);
    &--desc {
      position: relative;
      z-index: 2;
    }
    &--animate {
      position: absolute;
      display: flex;
      flex-direction: column;
      top: 0;
      left: 0;
      z-index: 1;
      @include block(120%, 100%);
      & span {
        display: block;
        flex-basis: 33.3%;
        background-color: var(--red);
        transform: translateX(-100%);
        animation-name: translate;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        animation-duration: 4s;
        @keyframes translate {
          0% {
            transform: translateX(-110%) skew(-45deg);
          }
          20% {
            transform: translateX(-110%) skew(-45deg);
          }
          40% {
            transform: translateX(-10%) skew(-45deg);
          }
          60% {
            transform: translateX(-10%) skew(-45deg);
          }
          80% {
            transform: translateX(-110%) skew(-45deg);
          }
          100% {
            transform: translateX(-110%) skew(-45deg);
          }
        }
        &:first-child {
          animation-delay: 0.15s;
        }
        &:nth-child(2) {
          animation-delay: 0.3s;
        }
        &:nth-child(3) {
          animation-delay: 0.45s;
        }
      }
    }
  }
  @include mobile {
    margin-bottom: s-md(125);
    &__container {
      flex-direction: column;
      border-radius: s-md(10);
      padding: 0;
      & > svg {
        @include block(100%, s-md(79));
      }
    }
    &__box {
      &--left,
      &--right {
        flex-basis: initial;
      }
      &--left {
        padding: s-md(26) s-md(16) 0;
        border-right: none;
        & svg {
          position: absolute;
          bottom: s(-84);
          left: s(-98);
          @include block(s(1608), s(182));
        }
      }
      &--right {
        padding: 0 s-md(16);
      }
    }
    &__title {
      margin-bottom: s-md(10);
    }
    &__desc {
      max-width: initial;
      font-size: s-md(18);
      line-height: 128%;
      letter-spacing: 0.035em;
    }
    &__form {
      padding: s-md(38) 0 0;
      &-desc {
        margin-bottom: s-md(28);
        padding-top: s-md(12);
        font-size: s-md(18);
        line-height: 128%;
        letter-spacing: 0.035em;
      }
    }
    &__label {
      margin-bottom: s-md(15.5);
    }
    &__input {
      padding: s-md(8) 0;
      border-width: 0 0 s-md(1) 0;
      &,
      &::placeholder {
        font-size: s-md(14);
        line-height: 140%;
        letter-spacing: 0.045em;
      }
      & + span {
        @include block(100%, s-md(2));
      }
    }
    &__btn {
      margin-bottom: s-md(32);
      padding: s-md(15) s-md(24);
    }
  }
}
